const svg = require('./svg');

module.exports = function circle(r, cx, cy) {
  const elem = svg('circle');
  elem.setAttributeNS(null, 'cx', `${cx}`);
  elem.setAttributeNS(null, 'cy', `${cy}`);
  elem.setAttributeNS(null, 'r', `${r}`);
  return elem;
};